import { useEffect, useState } from "react";
import { Image, View } from "react-native";
import { ActivityIndicator } from "react-native-web";

const ScaledImage = (props) => {
  const [width, setWidth] = useState();
  const [height, setHeight] = useState();
  const [imageLoading, setImageLoading] = useState(true);

  useEffect(() => {
    Image.getSize(
      props.uri,
      (width1, height1) => {
        if (props.width && !props.height) {
          setWidth(props.width);
          setHeight(height1 * (props.width / width1));
        } else if (!props.width && props.height) {
          setWidth(width1 * (props.height / height1));
          setHeight(props.height);
        } else {
          setWidth(width1);
          setHeight(height1);
        }
        setImageLoading(false);
      },
      (error) => {
        console.log("ScaledImage,Image.getSize failed with error: ", error);
      }
    );
  }, []);

  return height ? (
    <View
      style={{
        height: height,
        width: width,
        borderRadius: 5,
        justifyContent: "center",
        alignItems: "center",
        marginLeft: props.marginLeft,
        zIndex: -1,
      }}
    >
      <Image
        source={{ uri: props.uri }}
        style={{ height: height, width: width, borderRadius: 5 }}
      />
    </View>
  ) : imageLoading ? (
    <View
      style={{
        height: height,
        width: width,

        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <ActivityIndicator size="large" />
    </View>
  ) : null;
};

export default ScaledImage;
